<template>
  <div>
    <img :src="imgshow" v-show="bgShow" alt="error" id="bg" ref="bg" />
    <div id="search">
      <div id="searchDiv">
        <el-input
          v-model="inputText"
          class="input-with-select"
          type="text"
          id="text"
          ref="text"
        >
          <el-select
            v-model="searchValue"
            placeholder="请选择"
            id="searchSelect"
            slot="append"
            width="10"
          >
            <el-option
              v-for="(item, index) in webTable"
              :key="item.value"
              :label="item.name"
              :value="index"
            >
            </el-option>
          </el-select>
        </el-input>
      </div>
      <div id="copyright">
        <p>Copyright © 2023 GuoLan-Fruket, All Rights Reserved</p>
      </div>
    </div>

    <div id="settingIcon">
      <img src="../assets/img/setting.png" alt="error" @click="drawer = true" />
    </div>
    <el-drawer
      title="我是标题"
      :visible.sync="drawer"
      :with-header="false"
      id="drawer"
    >
      <div id="setting">
        <h1>搜索设置</h1>
        <el-table :data="webTable" style="width: 100%" height="350">
          <el-table-column label="名称">
            <template slot-scope="scope">
              <el-popover trigger="hover" placement="top">
                <p>{{ scope.row.url }}</p>
                <span slot="reference">{{ scope.row.name }}</span>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column label="快捷键">
            <template slot-scope="scope">
              <span>{{
                scope.row.hotkey === "" ? "无" : scope.row.hotkey
              }}</span>
            </template>
          </el-table-column>
          <el-table-column width="150">
            <template slot="header">
              <el-button
                type="primary"
                plain
                size="mini"
                @click="addWebDialog = true"
                >添加网址</el-button
              >
            </template>
            <template slot-scope="scope">
              <el-button size="mini" @click="tableRowSetting(scope.$index)"
                >修改</el-button
              >
              <el-button
                size="mini"
                type="danger"
                @click="tableRowDelete(scope.$index)"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <div class="content" id="hotkey">
          <span>补充功能：&nbsp;</span>
          <el-button
            size="small"
            style="margin: 0 20px 0 0"
            @click="batchsearch(inputText)"
            >批量搜索</el-button
          >
          <el-select
            v-model="batchSearchArr"
            placeholder="请选择"
            id="selectEnter"
            size="small"
            multiple
            collapse-tags
          >
            <el-option
              v-for="(item, index) in webTable"
              :key="index"
              :label="item.name"
              :value="index"
            >
            </el-option>
          </el-select>
        </div>

        <h1>背景设置</h1>
        <div class="content">
          <table id="bgSetting">
            <tr>
              <td>
                <el-radio v-model="radio" label="1"
                  >随机&nbsp;
                  <el-button size="mini" @click="interfaceDialog = true"
                    >设置接口</el-button
                  ></el-radio
                >
              </td>
              <td>
                <el-radio v-model="radio" label="2"
                  >自定义
                  <div id="chooseFile">
                    <el-button size="mini" style="position: relative"
                      >上传图片
                      <input
                        type="file"
                        @change="changeFile"
                        accept=".png, .jpg, .jpeg"
                      />
                    </el-button>
                  </div>
                </el-radio>
              </td>
            </tr>
            <tr>
              <td>
                <el-radio v-model="radio" label="3"
                  >纯色&nbsp;
                  <el-color-picker
                    v-model="bgcolor"
                    size="mini"
                    id="colorPicker"
                  ></el-color-picker>
                </el-radio>
              </td>
              <td>
                <el-radio v-model="radio" label="4">无</el-radio>
              </td>
            </tr>
            <tr>
              <td>
                <el-checkbox v-model="bgIsFixed">背景固定</el-checkbox>
              </td>
              <td>
                <el-checkbox v-model="successSave">保存成功提示</el-checkbox>
              </td>
            </tr>
          </table>
        </div>
      </div>
    </el-drawer>

    <el-dialog
      title="修改快捷键"
      :visible.sync="hotkeySettingDialog"
      width="30%"
    >
      <el-input
        v-model="hotkeyStr"
        placeholder="请输入新的快捷键并点击确定"
      ></el-input
      ><br /><br />
      <el-popover placement="right" width="350" trigger="click">
        <p style="margin: 10px 0">
          请保证格式为：“修饰键”若干+普通键*1，中间用“+”隔开，无空格。如：
        </p>
        <div style="margin: 0 20px">
          <ul>
            <li>ctrl+shift+alt+c</li>
            <li>ctrl+alt+a</li>
            <li>F2</li>
          </ul>
        </div>
        <p style="margin: 10px 0">以下格式容易发生错误，不建议使用：</p>
        <div style="margin: 0 20px">
          <ul>
            <li>ctrl + q+c (中间有空格，且普通键数量不为1)</li>
            <li>ctrl+shift+q (应为ctrl+shift+Q)</li>
            <li>标点符号 (易出错)</li>
          </ul>
        </div>
        <el-button slot="reference" size="small" plain
          >快捷键格式说明</el-button
        >
      </el-popover>
      <span slot="footer" class="dialog-footer">
        <el-button @click="hotkeySettingDialog = false">取 消</el-button>
        <el-button type="primary" @click="changeHotkey">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="删除"
      :visible.sync="hotkeyDeleteDialog"
      width="30%"
    >
      <span>此操作将删除该网站相关信息及快捷键，无法恢复，是否继续?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="hotkeyDeleteDialog = false">取 消</el-button>
        <el-button type="primary" @click="webTable.splice(changeHotkeyIndex, 1);hotkeyDeleteDialog = false;">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog title="添加网址" :visible.sync="addWebDialog" width="30%">
      <el-form ref="form" :model="form" label-width="40px">
        <el-form-item label="名称">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="url">
          <el-input v-model="form.url"></el-input>
        </el-form-item>
        <el-form-item>
          <el-popover placement="right" width="480" trigger="click">
            <p>
              输入网址时注意需带上网址后附带的参数。如添加百度时，先搜索“123”
            </p>
            <img src="../assets/img/exp.png" alt="error" style="width: 100%" />
            <p>“?”及之前的保留，寻找“123”对应的参数，这里为“wd”，拼接得</p>
            <p>https://www.baidu.com/s?wd= 即为正确的url格式</p>
            <el-button slot="reference" size="small" plain
              >添加网址说明</el-button
            >
          </el-popover>
        </el-form-item>
        <el-form-item>
          <el-button @click="addWebDialog = false">取消</el-button>
          <el-button type="primary" @click="addWeb">确定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog title="设置接口" :visible.sync="interfaceDialog" width="30%">
      <el-input v-model="imgurl"></el-input>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Main",
  data() {
    return {
      drawer: false,
      inputText: "",
      searchValue: -1,
      webTable: [
        {
          name: "B站",
          url: "https://search.bilibili.com/all?keyword=",
          hotkey: "alt+l",
          go(text) {
            window.open(this.url + text, "_blank");
          },
        },
        {
          name: "csdn",
          url: "https://so.csdn.net/so/search?q=",
          hotkey: "alt+c",
          go(text) {
            window.open(this.url + text, "_blank");
          },
        },
        {
          name: "博客园",
          url: "https://zzk.cnblogs.com/s?w=",
          hotkey: "alt+k",
          go(text) {
            window.open(this.url + text, "_blank");
          },
        },
        {
          name: "百度搜索",
          url: "https://www.baidu.com/s?wd=",
          hotkey: "alt+b",
          go(text) {
            window.open(this.url + text, "_blank");
          },
        },
        {
          name: "谷歌搜索",
          url: "https://www.google.com.hk/search?q=",
          hotkey: "alt+g",
          go(text) {
            window.open(this.url + text, "_blank");
          },
        },
        {
          name: "百度学术",
          url: "https://xueshu.baidu.com/s?wd=",
          hotkey: "alt+x",
          go(text) {
            window.open(this.url + text, "_blank");
          },
        },
        {
          name: "谷歌学术",
          url: "https://scholar.google.com/scholar?q=",
          hotkey: "alt+x",
          go(text) {
            window.open(this.url + text, "_blank");
          },
        },
        {
          name: "万方",
          url: "https://s.wanfangdata.com.cn/paper?q=",
          hotkey: "alt+x",
          go(text) {
            window.open(this.url + text, "_blank");
          },
        },
      ],
      batchSearchArr: [],
      radio: "1",
      bgShow: true,
      imgshow: "https://api.ixiaowai.cn/api/api.php",
      imgurl: "https://api.ixiaowai.cn/api/api.php",
      imgurl2: "",
      bgIsFixed: true,
      hotkeySettingDialog: false,
      hotkeyDeleteDialog: false,
      hotkeyStr: "",
      changeHotkeyIndex: 0,
      addWebDialog: false,
      form: {
        name: "",
        url: "",
      },
      interfaceDialog: false,
      bgcolor: "#FFF",
      successSave: true,
    };
  },
  methods: {
    batchsearch(text) {
      this.batchSearchArr.forEach((element) => {
        this.go(text, this.webTable[element].url);
      });
    },
    changeFile(e) {
      let reader = new FileReader();
      let file = e.target.files[0];
      if (file.type != "image/jpeg" && file.type != "image/png") {
        this.$message({
          showClose: true,
          message: "只接受格式为.png .jpg .jpeg的图片哦",
          type: "error",
        });
        return;
      }
      reader.readAsDataURL(file);
      reader.onload = (e) => {
        this.imgurl2 = e.target.result; // 图片地址 Base64 格式的 可预览
        this.radio = "2";
        this.imgshow = this.imgurl2;
        this.$message({
          showClose: true,
          message: "上传图片成功",
          type: "success",
        });
      };
    },
    storageData() {
      localStorage.setItem(
        "GuoLanNavigate",
        JSON.stringify({
          webTable: this.webTable,
          batchSearchArr: this.batchSearchArr,
          bgShow: this.bgShow,
          radio: this.radio,
          imgshow: this.imgshow,
          imgurl: this.imgurl,
          imgurl2: this.imgurl2,
          bgIsFixed: this.bgIsFixed,
          form: this.form,
          bgcolor: this.bgcolor,
          successSave: this.successSave,
        })
      );
    },
    tableRowSetting(index) {
      this.hotkeySettingDialog = true;
      this.changeHotkeyIndex = index;
    },
    tableRowDelete(index) {
      this.hotkeyDeleteDialog = true;
      this.changeHotkeyIndex = index;
    },
    hotkeyHandler(e) {
      this.webTable.forEach((element) => {
        let hotkeyArr = element.hotkey.split("+");
        let ctrl = false,
          alt = false,
          shift = false,
          win = false,
          key = "";
        for (let i = 0; i < hotkeyArr.length; i++) {
          if (hotkeyArr[i] == "ctrl") {
            ctrl = true;
          } else if (hotkeyArr[i] == "alt") {
            alt = true;
          } else if (hotkeyArr[i] == "shift") {
            shift = true;
          } else if (hotkeyArr[i] == "win") {
            win = true;
          } else {
            key = hotkeyArr[i];
          }
        }
        if (
          e.ctrlKey == ctrl &&
          e.altKey == alt &&
          e.metaKey == win &&
          e.shiftKey == shift &&
          e.key == key
        ) {
          this.go(this.inputText, element.url);
        }
      });
    },
    changeHotkey() {
      this.webTable[this.changeHotkeyIndex].hotkey = this.hotkeyStr;
      this.hotkeySettingDialog = false;
      this.hotkeyStr = "";
    },
    addWeb() {
      var name = this.form.name;
      var url = this.form.url;
      var obj = {
        name: name,
        url: url,
        hotkey: "",
      };
      this.webTable.push(obj);
      this.form.name = "";
      this.form.url = "";
      this.addWebDialog = false;
    },
    go(text, url) {
      window.open(url + text, "_blank");
    },
  },
  mounted() {
    this.$refs.text.focus();
    let tmpData = JSON.parse(localStorage.getItem("GuoLanNavigate"));
    if (tmpData !== null) {
      for (let key in tmpData) {
        this[key] = tmpData[key];
      }
    }
    window.addEventListener("keyup", this.hotkeyHandler);
  },
  watch: {
    searchValue(newVal) {
      if (newVal != -1) {
        this.go(this.inputText, this.webTable[newVal].url);
        this.searchValue = -1;
      }
    },
    radio(newVal) {
      if (newVal === "4") {
        this.bgShow = false;
        document.body.style.backgroundColor = "#FFF";
      } else if (newVal === "3") {
        this.bgShow = false;
        document.body.style.backgroundColor = this.bgcolor;
      } else {
        this.bgShow = true;
        if (newVal === "1") {
          this.imgshow = this.imgurl;
        } else if (newVal === "2" && this.imgurl2 !== "") {
          this.imgshow = this.imgurl2;
        }
      }
    },
    bgcolor(newVal) {
      document.body.style.backgroundColor = newVal;
      this.bgShow = false;
      this.radio = "3";
    },
    bgIsFixed(newVal) {
      this.$refs.bg.style.position = newVal ? "fixed" : "absolute";
    },
    drawer(newVal) {
      if (!newVal) {
        this.storageData();
        if (this.successSave) {
          this.$message({
            showClose: true,
            message: "成功保存数据到本地",
            type: "success",
          });
        }
      }
    },
  },
};
</script>

<style>
* {
  padding: 0;
  margin: 0;
}

#bg {
  width: 100%;
  position: fixed;
  z-index: -1;
}

#search {
  text-align: center;
}

#searchDiv {
  width: 450px;
  margin: 40px 0;
  display: inline-block;
  opacity: 0.8;
}

#text {
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
}

#settingIcon {
  position: absolute;
  top: 40px;
  right: 40px;
  cursor: pointer;
}

#settingIcon img {
  width: 50px;
  opacity: 0.6;
}

#setting {
  margin: 20px;
}

.content {
  margin: 5px 10px;
  /* font-size: 18px; */
  font-family: Arial, Helvetica, sans-serif;
}

#hotkey p {
  margin: 2px 80px 2px 0;
}

h1 {
  margin: 15px 0 0;
}

#chooseFile {
  display: inline-block;
  /* position: relative; */
  cursor: pointer;
}

#chooseFile input {
  cursor: pointer;
  width: 100%;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

#bgSetting td {
  width: 200px;
  height: 35px;
}

#colorPicker {
  vertical-align: middle;
}

#copyright {
  position: fixed;
  bottom: 5px;
  text-align: center;
  width: 100%;
  font-size: 10px;
}

#saveBtn {
  width: 100%;
  /* margin: 10px; */
  text-align: center;
}
</style>